import React, { createContext } from 'react';
import { Switch, Route, Router, Link } from 'react-router-dom';
import './App.css';
import Home from "./Home";
import SignUp from "./SignUp";
import Welcome from "./Welcome";
import NavBar from "./NavBar/NavBar"
import Fetchv from './Practice/Fetch';
import Video1 from './NavBar/Video';
import { mData } from './NavBar/Data1';

export const Xyz = createContext(null);

function App() {
  return (
    <>
    <Switch>
      <Route path="/" exact component= {Home} />
      <Route path="/SignUp" exact component = {SignUp} />
      <Route path="/Welcome" exact component = {Welcome} />
      <Route path="/NavBar" exact component= {NavBar} />
      <Route path="/Fetch" exact component={Fetchv}></Route>
      <Xyz.Provider value={{mData}}>
      <Route path="/Anime/:anime" component={Video1} />
      </Xyz.Provider>
      <Route path="/App" exact component={App}/>
    </Switch>
    </>
  );
}

export default App;
