import React, { useContext, useEffect, useRef, useState } from "react";
import {Figure} from 'react-bootstrap';
import { Link } from "react-router-dom";
import { Content } from "./NavBar";

const Anime = () => {
  const context = useContext(Content);
  const [heel, setheel] = useState('grid')
  const [leftheel, setleftheel] = useState('grid')
  const ref = useRef(null);
    const executeScroll = (scrolloffset) => {
      ref.current.scrollLeft += scrolloffset;
    }
    function ll(){
      ref.current.scrollLeft+ref.current.clientWidth===ref.current.scrollWidth? setheel('none') : setheel('grid');
      ref.current.scrollLeft===0? setleftheel('none') : setleftheel('grid');
    }
    useEffect(() => {
      ll(); 
    }, [])
    return(
        <> 
{context.item5.Name.map((Name) => {
  return <div className="Main2" ref={ref} onScroll={ll}> 
  <div className="Title">
<h1>Popular {Name}</h1>
</div>
  <div className="dd">
<div className="Left" onClick={() => executeScroll(-200)} style={{display: `${leftheel}`}}>
    <h5>{'\u2039'}</h5>
    </div> 
<div className="PopularAnime" >
{context.item5.contents.map((item, index) => {
                return <Figure key={index} className="aj" >
                <Figure.Image width={200} height={300} alt="171x180" src={`./Images/AnimeImages/${Name}/${item}.jpg`}/>
                <Figure.Caption style={{textAlign: 'center'}}>
                  <Link to={`${Name}/${item}`}>{item}</Link>
                </Figure.Caption>
              </Figure>
              })}
              </div>
              <div className="Right" onClick={() => executeScroll(200)} style={{display: `${heel}`}}>
                <h5>{'\u203A'}</h5>
              </div>
              </div>
</div>
})}
    </>)
} 

export default Anime;